body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,html{
    margin: 0;
    padding: 0;
    font-size: 15px;
    line-height: 26px;
    color: #333;

}

*{
    box-sizing: border-box;
}

.container{
    width: 90%;
    height: auto;
    padding: 0;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
}

:root{
  --maincolor :#1880a0;
  --secondcolor : #ff0000;
}

/********************** Header **************************/

.header{
    width: 100%;
    height: auto;
    padding: 12px 0;
    border-bottom: 1px solid #e3e4e6;
}


.navbar {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo h2 {
    font-size: 23px;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

.call,.email {
  display: flex;

}

.call p,.email p{
   margin: 0;
   padding: 0;
   display: flex;
   font-size: 14px;


}

.call span,.email span{
    color: #1880a0;
    color: var(--maincolor);
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    display: flex;
    margin: 0 5px;
    align-items: center;
}

.icons{
    font-size: 20px!important;
    margin-right: 3px;
}

.menu{
    font-size: 30px!important;
    color: #000!important;
    cursor: pointer;
}

.nav{
  text-decoration: none;
  color: #333;
}

.hamburg{
    display: flex;
    align-items: center;
}



.MuiList-root{
    width: 239px;
    height: auto;
    
}

/**************************** Hero *******************************/

.hero{
    width: 100%;
    height: auto;
    padding: 20px 0;
    background-color: #e3e4e6;;
}

.hero_wrap{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
}

.hero_box{
    width: 50%;
    height: auto;
    padding: 0;

}

.hero_box h1{
    margin: 0;
    padding: 0;
    font-size: 2.1rem;
    font-weight: 400;
    color: #333;
    text-transform: uppercase;
    line-height: 46px;

}

.hero_box h2{
    margin: 0;
    padding: 0;
    font-size: 3rem;
    font-weight: 700;
    color: #1880a0;
    color: var(--maincolor);
    text-transform: uppercase;
    line-height: 38px;

}
.hero_box p{
    margin: 30px 0;
    padding: 0;
    font-size: 1rem;
    font-weight: 400;
    color: #333;
    text-transform: capitalize;
 }

.readmore{
    background-color: #ff0000!important;
    background-color: var(--secondcolor)!important;
    border-radius: 0!important;
    margin-right: 10px!important;
}
.contact{
    background-color: #1880a0!important;
    background-color: var(--maincolor)!important;
    border-radius: 0!important;
}

 .hero_box img{
     width: 87%;
     height: auto;
   
 }

/***************** whatwedo ********************/

.whatwedo{
    width: 100%;
    height: auto;
    padding: 60px 0 30px;
}

.whatwe_wrap{
    width: 100%;
    height: auto;
    padding: 0;
    text-align: center;
}

.whatwe_wrap h3{
    font-size: 27px;
    font-weight: 500;
    color: #333;
    margin: 10px 0;
    text-transform: uppercase;
}

.card_wrap{
    width: 100%;
    height: auto;
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
}

.cards{
    width: 28%;
    height: auto;
    padding: 0;
   
}
.cinos{
    font-size: 35px!important;
    color: #1880a0;
    color: var(--maincolor);
}
.card h6{
    font-size: 16px;
    margin: 10px 0;
    font-weight: 700;
    color: #333;
    text-transform: uppercase;
}
.card p{
    font-size: 15px;
    margin: 0;
    padding: 0;
}

.abtn{
    margin-top: 25px;
    text-decoration: none;
    display: block;

}
.abtn button{
    text-transform: uppercase;
    color: #1880a0;
    color: var(--maincolor);
    font-size: 14px;
    font-weight: 600;
}

/******************** about **********************/

.about{
    width: 100%;
    height: auto;
    padding: 0;
    margin-top: 60px;
    margin-bottom: 30px;
    display: block;
    background-color: #1880a0;
    background-color: var(--maincolor);

}

.img_fluid{
    width: 100%;
    height: auto;
    display: flex;

}



.css-a5rdam-MuiGrid-root>.MuiGrid-item {
    padding-top: 0px!important;
}

.about_one{
    width: 90%;
    margin-left: auto;
    height: 48vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    
}

.about_card{
    display: flex;
}

.about_card h2{
    font-size: 2rem;
    font-weight: 500;
    text-transform: uppercase;
    margin: 10px 0;
    color: #fff;
}

.about_card p{
    color: #fff;
}

.about_btn{
    border-color: #fff!important;
    color: #fff!important;
    margin-top: 30px!important;
}

/*******************  work ***********************/
.work{
    width: 100%;
    height: auto;
    padding: 0;
    margin: 0px 0 60px;
}

.wrok_wrap{
    width: 100%;
    height: auto;
    padding: 60px 0 40px;
    text-align: center;
}

.wrok_wrap h4{
    font-size: 27px;
    font-weight: 500;
    color: #333;
    margin: 10px 0;
    text-transform: uppercase;
}

.img_fluids{
    width: 100%;
    height: auto;
    max-height: 250px;
}
/********************** best ***********************/
.best{
    width: 100%;
    height: 50vh;
    padding: 0;
    text-align: center;
    background-image: url(/paint_company/static/media/callaction.c6e06ca0.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.best:before{
   content: "";
   width: 100%;
   height: 100%;
   background-color: rgba(24, 128, 160, 0.5);
   position: absolute;
   top: 0;
   left: 0;
  
}

.best_wrap{
    width: 80%;
    height: 100%;
    margin: 0 auto;
    z-index: 1759;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
 

}

.best_wrap p{
    font-size: 15px;
    color: #fff;
    margin: 40px 0;
    
}

.best_wrap h5{
    font-size: 27px;
    font-weight: 500;
    color: #fff;
    margin: 0px 0;
    text-transform: uppercase;
}

/******************** Customers *************************/

.customer{
    width: 100%;
    height: auto;
    padding: 60px 0;
    text-align: center;
}

.customer_wrap{
    width: 100%;
    height: auto;
}

.customer_wrap h2{
    font-size: 27px;
    font-weight: 500;
