    font-weight: 500;
}

.content{
    width: 60%;
    height: auto;
    text-align: left;
}

.arrowIcon{
    padding: 0 10px;
    cursor: pointer;
    font-size: 20px!important;
}

.Ifirst{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.getfree{
    width: 100%;
    height: auto;
    padding-top: 90px;

}

.get_wap{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.get_wap h3{
    font-size: 3rem;
    margin: 0;
    padding: 0;
    color: #333;
    text-transform: capitalize;
    width: 44%;
