/*************** Media queries *******************/

@media (min-width: 150px) and (max-width: 564px) {
  
    .container {
        flex-direction: column;
    }
    
    .navbar {
       flex-direction: column;
    }

    .footer_wrap {
        flex-direction: column;
    }
    .hero_wrap { 
      flex-direction: column;
    }

    .hero_box {
        width: 100%;
   }
   .hero {
   text-align: center;
  }
  .hero_box img {
    width: 100%;
    height: auto;
}


.card_wrap {
    flex-direction: column;
}

.cards {
    width: 100%;
     margin-bottom: 60px;
    text-align: center;
}

.about {
    width: 100%;
