    margin-right: 20px;
}

.get_wap { 
  flex-direction: column;
}

.get_wap h3 {
    width: 100%;
   margin-bottom: 30px;
}


.contactUs_wrap {
   flex-direction: column;
}

.contactbtn {
    margin-top: 20px!important;
    margin-bottom: 40px!important;
}

.padding {
    padding-right: 0px;
}


  }





  @media (min-width: 566px) and (max-width: 768px) {

 .container {
